<template>
  <div>
    <el-tabs
      v-model="activename"
      @tab-click="handleClick"
      tab-position="left"
    >
      <el-tab-pane
        label=""
        name="status"
      >
        <div>
          <p></p>
          <p>1.15%</p>
          <p>
            2.30%
          </p>
          <p>
            3.20%
          </p>
          <p>
            4.15%
          </p>
          <el-table
            :data="statusTabledata"
            border
            style="width: 100%"
          >
            <el-table-column
              prop="name"
              width="250"
              label=""
            >
              <div slot-scope="scope">
                <router-link to="">
                  {{ scope.row.name }}
                </router-link>
              </div>
            </el-table-column>

            <el-table-column
              width="180"
              prop="type"
              label=""
            />
            <el-table-column
              width="180"
              prop="status"
              label=""
            />
            <el-table-column
              prop="tips"
              label=""
            />
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        ref="addCourseref"
        name="addCourse"
      >
        <span slot="label">{{ addCourselabel }}</span>
        <el-form
          label-width="90px"
          :rules="rules"
          :inline="true"
          ref="courseFormref"
          :model="courseForm"
        >
          <el-form-item
            label=""
            prop="tc_name"
          >
            <el-input v-model="courseForm.tc_name" />
          </el-form-item>
          <el-form-item label="ID">
            <el-input v-model="courseForm.tc_showid" />
          </el-form-item>
          <el-form-item
            label=""
            prop="teach_ids"
          >
            <el-select
              v-model="courseForm.teach_ids"
              placeholder=""
            >
              <el-option
                v-for="item in teacheroptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            prop="tc_score"
          >
            <el-input
              type="number"
              min="0"
              v-model="courseForm.tc_score"
            />
          </el-form-item>
          <el-form-item
            class="texts"
            label=""
          >
            <editor
              id="editor"
              :catch-data="catchData"
              :content="courseForm.tc_describe"
            />
          </el-form-item>
          <el-form-item label="">
            <div class="form2">
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_usual_performance" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_report_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_experimental_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_training_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_interim_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_extracurricular_performance" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_final_exam" />
              </el-form-item>
              <el-tag type="warning">
                
              </el-tag>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="formbtn">
              <el-button @click="submitForm('courseFormref')">
                
              </el-button>
              <el-button @click="closeForm()">
                
              </el-button>
            </div>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane
        name="coures"
        label=""
      >
        <div>
          <el-table
            :data="courselist"
            stripe
            style="width: 100%"
          >
            <el-table-column
              prop="tc_name"
              label=""
              width="180"
            />
            <el-table-column
              prop="tc_showid"
              label="ID"
              width="180"
            />
            <el-table-column
              prop="tc_score"
              label=""
            />
            <el-table-column label="">
              <div slot-scope="scope">
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="delcourse(scope.row)"
                />
                <el-button
                  type="warning"
                  size="mini"
                  icon="el-icon-edit"
                  @click="alter(scope.row)"
                />
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-share"
                  @click="setknowledgepoint(scope.row)"
                />
              </div>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        name="help"
        label=""
      >
        <h3></h3>
        <p></p>
        <h3></h3>
        <p>\</p>
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      title=""
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      width="80%"
    >
      <div />
      <div class="card1">
        <span></span>
        <el-collapse @change="handleChange">
          <el-collapse-item>
            <template
              slot="title"
              class="card-style"
            >
              <i class="iconfont icon-jiahao" />
              <div class="kPitem">
                <el-badge :value="12">
                  <span></span>
                </el-badge>
                <div class="itembtn">
                  <el-button
                    type="warning"
                    size="mini"
                    icon="el-icon-edit"
                    @click.stop="editkp"
                  >
                    
                  </el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    @click.stop=""
                  >
                    
                  </el-button>
                </div>
              </div>
            </template>
            <el-card class="kPbody">
              <p></p>
              <div class="tableheader">
                
              </div>
              <div>
                <div class="tabletitle">
                  123
                </div>
                <div class="tableitem">
                  123
                </div>
              </div>
            </el-card>
          </el-collapse-item>
        </el-collapse>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible1 = false"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      width="80%"
    >
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible2 = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import editor from './component/editor'
export default {
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      addCourselabel: '',
      isalter: false,
      activename: 'status',
      courseForm: {
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      },
      courselist: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      teacheroptions: [],
      statusTabledata: [
        { name: '', type: '', status: '', tips: '' },
        { name: '', type: '', status: '', tips: '	' },
        { name: '', type: '', status: '', tips: '	' }
      ],
      rules: {
        tc_name: [{ required: true, message: '', trigger: 'blur' }],
        tc_id: [{ required: true, message: 'ID', trigger: 'blur' }],
        tc_teacher: [{ required: true, message: '', trigger: 'blur' }],
        tc_score: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getcourselist()
    this.getteacherlist()
  },
  mounted () {},
  methods: {
    handleClick (tab, e) {
      this.$emit('getpath1', tab.label || this.addCourselabel)
      if (this.activename != 'addCourse') {
        this.addCourselabel = ''
        this.isalter = false
        // 
        this.courseForm = {
          tc_describe: '',
          tc_usual_performance: 0,
          tc_experimental_results: 0,
          tc_interim_results: 0,
          tc_final_exam: 100,
          tc_report_results: 0,
          tc_training_results: 0,
          tc_extracurricular_performance: 0
        }
      }
    },
    async getcourselist () {
      const { data: res } = await this.$http.get('course/showAll')
      this.courselist = res.obj.list
    },
    async getteacherlist () {
      const { data: res } = await this.$http.get('admin/showTeacher')
      res.obj.some(item => {
        const obj = { label: item.u_name, value: item.u_id }
        this.teacheroptions.push(obj)
      })
    },

    // 
    async setknowledgepoint (row) {
      this.dialogVisible1 = true
      const tc_id = row.tc_id
      const { data: res } = await this.$http.post('kpc/cid', { tc_id })
      console.log(res)
    },
    // 
    alter (row) {
      this.addCourselabel = ''
      this.$emit('getpath1', this.addCourselabel)
      this.activename = 'addCourse'
      this.isalter = true
      row.teach_ids = row.tc_teacher.u_id
      delete row.tc_teacher
      this.courseForm = row
    },
    // 
    async delcourse (row) {
      console.log(row.tc_id)
      const tc_id = row.tc_id
      // 
      const confirmres = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.post('course/del', { tc_id })
      console.log(res)
      this.getcourselist()
    },
    // 
    closeForm () {
      this.addCourselabel = ''
      this.$emit('getpath1', this.addCourselabel)
      this.activename = 'coures'
      this.isalter = false
      // 
      this.courseForm = {
        tc_describe: '',
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      }
    },
    // 
    submitForm (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (!isalter) {
          const { data: res } = await this.$http.post('course/add', this.courseForm)
          return false
        }
        const { data: res } = await this.$http.post('course/update', this.courseForm)
      })
    },
    catchData () {},
    editkp () {
      this.dialogVisible2 = true
    },
    async delkp (tkp_id) {
      const { data: res } = await this.$http.post('kpc/del', { tkp_id })
      console.log(res)
    },
    handleClose1 () {
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.dialogVisible2 = false
    },
    handleChange () {
      setTimeout(() => {
        const right = document.getElementsByClassName('el-collapse-item__arrow el-icon-arrow-right')
        right.forEach(element => {
          if (element.className === 'el-collapse-item__arrow el-icon-arrow-right is-active') {
            element.previousSibling.previousSibling.className = 'iconfont icon-jianhao'
          } else {
            element.previousSibling.previousSibling.className = 'iconfont icon-jiahao'
          }
        })
      }, 50)
    }
  },
  components: {
    editor
  }
}
</script>
<style lang="less" scoped>
.el-form {
  padding: 0 120px;
  > .el-form-item:nth-child(-n + 4) {
    margin-right: 175px;
  }
}
.form2 {
  width: 839px;
  border: 1px solid #e1e1e1;
  padding: 20px;
  .el-form-item {
    margin-right: 105px;
  }
}
.el-form > .el-form-item:last-child {
  width: 100%;
  text-align: center;
}
.formbtn {
  width: 250px;
  display: flex;
  justify-content: space-between;
}
#editor {
  width: 839px;
  position: relative;
  z-index: 0;
}
.el-select {
  width: 202px;
}
.card1 {
  display: flex;
  justify-content: space-between;
  span {
    line-height: 28px;
  }
}
.el-collapse {
  border: none;
  width: 85%;
}
.kPitem {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.kPbody {
  p {
    padding-left: 5px;
    border-left: 2px solid #337ab7;
  }
}
.tableheader {
  border: 1px solid #e1e1e1;
  border-radius: 4px 4px 0 0;
  padding: 8px;
}
.el-card {
  margin-top: 10px;
}
.el-collapse-item {
  margin-bottom: 10px;
}
.tabletitle,
.tableitem {
  border: 1px solid #e1e1e1;
  padding: 10px;
}
.tabletitle {
  height: 40px;
  background-color: #f5f7fa;
}
.tableitem {
}
</style>
